import { EditorContent, useEditor } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import BoldButton from './BoldButton';
import ItalicButton from './ItalicButton';
import UnderlineButton from './UnderlineButton';
import Underline from '@tiptap/extension-underline';



const TiptapEditor = () => {
  const editor = useEditor({
    extensions: [StarterKit, Underline],
    content: '',
  });

  if (!editor) {
    return null;
  }

  return (
    <>
      <BoldButton editor={editor} />
      <ItalicButton editor={editor} />
      <UnderlineButton editor={editor} />
      



      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '100vh', // Full viewport height
          backgroundColor: '#f0f0f0', // Light gray background
        }}
      >




        <div
          style={{
            width: '80%', // 80% of the viewport width
            height: '80%', // 80% of the viewport height
            padding: '16px', // Internal padding
            backgroundColor: '#ffffff', // White background
            border: '2px solid #ccc', // Light gray border
            borderRadius: '12px', // Rounded corners
            fontSize: '16px', // Font size for readability
            boxShadow: '0 4px 10px rgba(0, 0, 0, 0.15)', // Subtle shadow
            overflowY: 'auto', // Enable vertical scrolling for overflow content
            display: 'flex',
            alignItems: 'flex-start', // Align content to the top of the editor
            outline: 'none', // Remove outline on the text area
          }}
          onClick={() => editor?.commands.focus()} // Focus editor on click
        >
          <EditorContent
            editor={editor}
            style={{
              width: '100%',
              minHeight: '100%',
              outline: 'none', // Remove outline on the text area
              cursor: 'text', // Text cursor for the editor
              whiteSpace: 'pre-wrap', // Preserve whitespace and wrap text
              border: 'none', // No border
              boxShadow: 'none', // No box shadow
            }}
          />

        </div>
      </div>
    </>
  );
};

export default TiptapEditor;
